header{
    background-color: #eaefe6;
    -webkit-box-shadow: 0 3px 7px 0 rgb(0 0 0 / 10%);
            box-shadow: 0 3px 7px 0 rgb(0 0 0 / 10%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    top: 0px;
    padding: 20px;
    width: 100%;
    z-index: 10000;
}

a{
    color: black!important;
    text-decoration: none!important;
    cursor: pointer;
}

a.menu-item{
    color: gray!important;
    font-size: 17px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

a.menu-item:hover{
    color: black!important;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.logo{
    font-family: optima;
    font-size: 35px;
    margin: 5px 10px 5px 25px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.logo:hover{
    text-shadow: 0px 0px 2px gray;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}






