@-webkit-keyframes blur-animated{
    from{
        opacity: 0;
        -webkit-filter:blur(10px);
                filter:blur(10px);
    }
    to{
        opacity: 100%;
        -webkit-filter: blur(0px);
                filter: blur(0px);
        
    }
}

@keyframes blur-animated{
    from{
        opacity: 0;
        -webkit-filter:blur(10px);
                filter:blur(10px);
    }
    to{
        opacity: 100%;
        -webkit-filter: blur(0px);
                filter: blur(0px);
        
    }
}

@-webkit-keyframes text-shadow{
    from{
        color: white;
    }
    to{
        color: black;
    }
}

@keyframes text-shadow{
    from{
        color: white;
    }
    to{
        color: black;
    }
}

.MainSection{
    height: 614px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: hidden;
    margin-top: 100px;
    padding-top: 40px;
    text-align: center;
}

.welcome{
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: text-shadow;
            animation-name: text-shadow;
    font-family: "Bradley Hand";
    font-size: 50px;
}

.border{
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: blur-animated;
            animation-name: blur-animated;
    border: 0!important;
    display: block;
    left: 38%;
    top: 40px;
    position: relative;
    width: 380px;
}


