@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');

@-webkit-keyframes github-spin-in{
    from {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}

@keyframes github-spin-in{
    from {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}

@-webkit-keyframes github-spin-out{
    0% {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
}

@keyframes github-spin-out{
    0% {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
}

@-webkit-keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@-webkit-keyframes rotateMenu {

    0% {
        
      -webkit-transform: rotateX(-90deg);
        
              transform: rotateX(-90deg)
  
    }
  
    70% {
  
      -webkit-transform: rotateX(20deg);
  
              transform: rotateX(20deg)
  
    }
  
    100% {
  
      -webkit-transform: rotateX(0deg);
  
              transform: rotateX(0deg)
  
    }
  
}

@keyframes rotateMenu {

    0% {
        
      -webkit-transform: rotateX(-90deg);
        
              transform: rotateX(-90deg)
  
    }
  
    70% {
  
      -webkit-transform: rotateX(20deg);
  
              transform: rotateX(20deg)
  
    }
  
    100% {
  
      -webkit-transform: rotateX(0deg);
  
              transform: rotateX(0deg)
  
    }
  
}

.github{
    -ms-flex-align: center;
        align-items: center;
    background-color: white;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    height: 420px;
    -ms-flex-pack: center;
        justify-content: center;
    overflow: hidden;
    text-align: center;
    width: 100%;
}

.github-container{
    width: 50%;
}

.icon{
    width: 10%;
}

.github-logo{
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: github-spin-out;
            animation-name: github-spin-out;
    fill: gray;
    width: 30%;
    -webkit-transition: 0.7s ease-in-out;
    -o-transition: 0.7s ease-in-out;
    transition: 0.7s ease-in-out
}

.github-light{
    -webkit-animation: float 2s ease-in-out infinite;
            animation: float 2s ease-in-out infinite;
    bottom: -2110px;
    left: 50px;
    position: absolute;
    width: 150px;
}

.github-cat{
    -webkit-animation: float 2s ease-in-out infinite;
            animation: float 2s ease-in-out infinite;
    bottom: -2270px;
    right: 50px;
    position: absolute;
    width: 150px;
}

.follow-me{
    font-family: 'Permanent Marker', cursive;
    font-size: 45px;
    margin-top: 20px;
}

.github-logo-out{
    fill: gray;
    width: 100%;
}

.github-logo:hover{
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: github-spin-in;
            animation-name: github-spin-in;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    fill: lightgreen;
    -webkit-transition: 0.7s ease-in-out;
    -o-transition: 0.7s ease-in-out;
    transition: 0.7s ease-in-out;
}



